*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0f0f22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  width: 95%;
  min-height: 100vh;
  position: relative;
  margin: 0 auto;
  text-align: center;
}

.container h1{
  color: white;
  text-align: center;
  padding: 15px;
  font-size: 40px;
  font-weight: bold;
}


.container .media-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 15px;
  margin: 20px auto;
}

.container .media-container .media {
  height: 220px;
  width: 100%;
  border: 5px solid white;
  box-shadow: 0 5px 15px grey;
  overflow: hidden;
  cursor: pointer;
}

.container .media-container .media img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: all 0.5s linear;
}

.container .media-container .media.first-image {
  height: 98.8%;
  width: 100%;
  object-fit: cover;
  grid-column: span 2;
  grid-row: span 3;
}


.container .media-container .media.first-image img {
  transform: scale(1.5); 
}

.container .media-container .media:hover img {
  transform: scale(1.3);
}



.container .media-container .media {
 
  transition: transform 0.5s ease-in-out;
}

.container .media-container .media.dragged {
  opacity: 0.7; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); 
}



.container .media-container .media.selected {
  border: 5px solid red; 
  opacity: 0.3;
}

.btn{
  padding: 20px;
  background-color: red;
  font-size: large;
  font-weight: bold;
  color: white;
  text-align: center;
}
