.container .add-image-box {
  height: 220px; /* Match the height of other image boxes */
  width: 100%;
  background-color: grey;
  color: black;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.container .add-image-box:hover {
  background-color: darkgrey;
}

.imageSelection{
  color: white;
  text-align: center;
  padding: 15px;
  font-size: 40px;
  font-weight: bold;
}